@import './styles/styles.scss';

.mobile-container.homescreen-container{
    display: grid;
    grid-template-rows: 60px 1fr;
    height: 100vh;
}

.homescreen-container{
    /* navbar starts */
    
    .navbar{
        z-index: 9999;
        display: grid;
        grid-template-columns: 44px 1fr;
        grid-column-gap: 30px;
        align-content: center;
        justify-content: space-around;
        padding: 0 20px;

        width: 100vw;
        height: 60px;
        background-color: #FFFFFF;
        box-shadow: 0 2px 19px 0 rgba(26, 24, 24, 0.14);

            
        /* navbar drawer toggler starts */
        .brand.show-drawer{
            align-self: center;
            img{
                width: 44px;
                height: 28px;
            }
        }
        /* navbar drawer toggler ends */
        .nav-search{
            border: 1px solid #EAEAEA;
            border-radius: 3px;
            display: grid;
            grid-template-columns: 1fr 8fr;
            grid-gap: 10px;
            align-items: center;
            padding: 10px;

            .search-icon{
                display: flex;
                justify-self: center;
            }
            .search-input{
                input{
                    width: 100%;
                    border: 0;
                    outline: 0;
                }
            }
        }
    }
    /* navbar ends */
    .homescreen-contents{
        display: grid;
        grid-row-gap: 20px;
        background: #E2E2E2;
        overflow-y: scroll;
        /* card global styling */
        .card-wrapper{
            background: #FFF;
            box-shadow: 0 1px 8px 0 #D6D6D6;
            padding: 30px 20px;
        }
        /* card global ends */

        /* card welcome starts */
        .card-wrapper.card-user-welcome{
            display:  grid;
            grid-row-gap: 10px;
            .content-image{
                img{
                    height: 20px;
                }
            }
            .user-name{
                @include h1($font-size: 20px);
            }
        }
        /* card welcome ends */

        /* card getting started starts */
        .card-wrapper.card-getting-started{
            display: grid;
            grid-template-columns: 1fr 1fr;
            grid-gap: 30px;
            background: linear-gradient(134.98deg, #008CAA 0%, #00BE6E 100%);
            .main-title{
                grid-area: 1 / 1 / 1 / -1 ;
                @include h1($font-size: 20px, $color: #FFF);
            }
            .content-image{
                justify-self: center;
                .image-tombstone{
                    img{
                        width: 160px;
                        height: 135px;
                    }
                }
            }
            .button-take-tour{
                justify-self: end;
                align-self: end;

                button{
                    @include help-text($font-family: Nunito-SemiBold, $color: #FFF);                    
                    padding: 10px 20px;
                    border: 2px solid #FFFFFF;
                    border-radius: 3px;
                    background: none;
                }
            }
        }
        /* card getting started ends */

        /* card browse all circles starts */
        .card-wrapper.card-all-outreachcircles{
            display: grid;
            grid-gap: 30px;

            .main-title{
                @include h1($font-size: 20px, $color: #2A2A2A);
            }
            .content-image{
                justify-self: center;
                .image-tombstone{
                    img{
                        width: 205px;
                        height: 175px;
                    }
                }
            }
            .footer-browse-all{
                @include help-text($font-family: Nunito-SemiBold, $color: #00BE6E);
                display: flex;
                justify-content: space-between; 
                align-items: center;                   
                border-top: 1px solid #E9E9E9;
                border-bottom: 1px solid #E9E9E9;
                margin: 0 -20px;
                padding: 10px 20px;
            }
        }
        /* card browse all circles ends */

        
        /* card actions starts */
        .card-wrapper.card-action{
            display:  grid;
            grid-row-gap: 10px;
            padding: 10px 20px;

            .action-header{
                display: grid;
                grid-template-columns: 66px  1fr;
                grid-gap: 10px;
                align-items: center;
                .action-thumbnail{
                    background-color: #D8D8D8;
                    align-self: center;

                    img{
                        width: 100%;
                    }
                }
                .action-info{
                    display: grid;
                    grid-template-columns: auto auto;
                    grid-template-rows: auto auto auto;
                    border-bottom: 1px solid #EAEAEA;

                    .action-user{
                        @include h1($font-size: 12px);
                    }
                    .action-outreachcircle{
                        @include h1($font-size: 12px);
                    }
                    .action-timestamp{
                        @include para1($font-size: 10px);
                        padding: 5px 0;
                    }
                    .action-more-menu{
                        position: relative;
                        grid-area: 1 / 2 / -1 / -1;
                        justify-self: end;
                        align-self: center;
                        color: #000;
                    }
                }
                
            }
            .action-body{
                display: grid;
                grid-gap: 10px;
                padding: 10px 0;

                .action-title{
                    @include h1($font-size: 16px, $color: #2A2A2A);
                }
                .action-description{
                    @include para3($font-size: 14px, $color: #2A2A2A);
                }
                .action-definition.video-preview{
                    display: grid;
                    .video-preview-icon{
                        grid-area: 1 / 1 / 1 / 1;
                        align-self: center;
                        justify-self: center;
                        color: #FFF;
                    }
                    .video-preview-image{
                        grid-area: 1 / 1 / 1 / 1;
                        img{
                            width: 100%;
                        }
                    }
                }
                .action-definition.facebook-preview{
                    display: grid;
                    .facebook-preview-spinner{
                        grid-area: 1 / 1 / 1 / 1;
                        align-self: center;
                        justify-self: center;
                    }
                    .facebook-preview-content{
                        grid-area: 1 / 1 / 1 / 1;
                    }
                }
                .action-definition.button-invite{
                    button{
                        @include h1($font-size: 16px, $color: #FFF);
                        background-color: #00BE6E;
                        border-radius: 3px;
                        border: 0;
                        width: 100%;                            
                        padding: 10px;
                    }
                }
            }
            .action-footer{
                @include para1($font-size: 12px, $color: #777);
                display: flex;
                flex-direction: row-reverse;
                grid-gap: 7px;
                .supporter-icon{
                    img{
                        width: 11px;
                        height: 13px;
                    }
                }
            }
        }
        /* card actions ends */

        /* card footer starts */
        .card-footer{
            @include help-text($color: #BCBCBC);

            display: flex;
            justify-content: center;
            align-items: center;
            grid-gap: 10px;
            background: #E2E2E2;
            box-shadow: 0 0 0;
            .logo-watermark{
                img{
                    width: 20px;
                    height: 20px;
                }
            }
        }
        /* card footer ends */
    }
}